body {
  font: Lato, sans-serif;
  color: #000000;
  font-weight: 200; }

.gap-top {
  margin-top: 20px; }

.gap-bottom {
  margin-bottom: 20px; }

.gap-right {
  margin-right: 20px; }

.hide {
  display: none; }

.has-error .dropdown button {
  border-color: #a94442; }

.no-padding-left {
  padding-left: 0; }

.no-padding-right {
  padding-right: 0; }

/* LIGHT FORM
-------------------------------------------------- */
.light-form-control {
  padding: 2px 4px; }

.light-input-group-addon {
  padding: 4px 4px; }

/* CUSTOMIZE PAGINATION
-------------------------------------------------- */
/* ARTICLE AREA
-------------------------------------------------- */
.article-area > p {
  padding: 0;
  margin: 0; }

/* Main
---------------------------------------------- */
.footer {
  bottom: 0;
  height: 60px;
  width: 100%;
  margin-top: 20px; }
  .footer .container {
    border-top: 1px solid #BDD7F5; }
  .footer .text-footer {
    margin: 20px 0; }

.label {
  font-size: 100%;
  font-weight: 100; }

[v-cloak] {
  display: none; }

/* NAVIGATION
-------------------------------------------------- */
.navbar {
  margin: 0; }

.color-line {
  height: 3px;
  background: -webkit-linear-gradient(left, #3023AE, #BDE3F5, #B4EC51);
  /* Safari 5.1-6.0 */
  background: -o-linear-gradient(right, #3023AE, #BDE3F5, #B4EC51);
  /* Opera 11.1-12.0 */
  background: -moz-linear-gradient(right, #3023AE, #BDE3F5, #B4EC51);
  /* Firefox 3.6 -15 */
  background: linear-gradient(to right, #3023AE, #BDE3F5, #B4EC51);
  /* Standard syntax */ }

.navbar-july {
  border-bottom-color: #BDD7F5; }

/* CAROUSEL
-------------------------------------------------- */
/* Carousel base class */
.carousel {
  height: -moz-calc(100vw * 0.4);
  height: -webkit-calc(100vw * 0.4);
  height: calc(100vw * 0.4);
  margin-bottom: 60px; }
  .carousel .carousel-indicators {
    margin-bottom: 0; }
  .carousel .carousel-inner {
    height: 100%; }
  .carousel .item {
    height: 100%;
    background-color: #FFFFFF; }
    .carousel .item .carousel-caption {
      padding-top: -moz-calc(5% / 2);
      padding-top: -webkit-calc(5% / 2);
      padding-top: calc(5% / 2);
      padding-bottom: -moz-calc(5% / 2);
      padding-bottom: -webkit-calc(5% / 2);
      padding-bottom: calc(5% / 2); }
      .carousel .item .carousel-caption h2 {
        font-size: 6vmin;
        padding: 0;
        margin: 0; }
    .carousel .item img {
      position: absolute;
      top: 0;
      left: 0;
      min-width: 100%;
      max-height: 100%; }

/* Carousel base class */
.carousel-indicators li {
  border-color: #4A90E2; }

.carousel-indicators .active {
  background-color: #4A90E2; }

/* Since positioning the image, we need to help out the caption */
.carousel-caption {
  z-index: 10;
  color: #fff; }
  .carousel-caption p {
    text-shadow: none; }

.carousel-control .fa {
  right: 50%;
  top: -moz-calc(50% - 0.5em);
  top: -webkit-calc(50% - 0.5em);
  top: calc(50% - 0.5em);
  display: inline-block;
  position: absolute;
  z-index: 5;
  color: #fff; }

.carousel-control.left {
  background-image: none; }

.carousel-control.right {
  background-image: none; }

/* RECOMMENDED
-------------------------------------------------- */
.col-lg-3 {
  text-align: center; }
  .col-lg-3 a {
    display: inline-block; }

.articles-recommended .row > div {
  height: 40vh;
  overflow-y: hidden; }

/* DETAIL
-------------------------------------------------- */
.detail .col-lg-6 {
  text-align: center; }
  .detail .col-lg-6 > .container-fluid {
    height: 240px; }
    .detail .col-lg-6 > .container-fluid .op {
      height: 100%;
      position: relative;
      font-size: 20pt; }
      .detail .col-lg-6 > .container-fluid .op > a {
        position: absolute;
        top: -moz-calc(50% - 0.5em);
        top: -webkit-calc(50% - 0.5em);
        top: calc(50% - 0.5em);
        left: -moz-calc(50% - 0.5em);
        left: -webkit-calc(50% - 0.5em);
        left: calc(50% - 0.5em); }
      .detail .col-lg-6 > .container-fluid .op > a:hover {
        text-decoration: none; }
      .detail .col-lg-6 > .container-fluid .op > a:focus {
        text-decoration: none; }
    .detail .col-lg-6 > .container-fluid > .swiper-container {
      position: relative;
      overflow: hidden;
      height: 240px; }
      .detail .col-lg-6 > .container-fluid > .swiper-container.animate > .pane {
        transition: all .3s;
        -webkit-transition: all .3s;
        -moz-transition: all .3s; }
      .detail .col-lg-6 > .container-fluid > .swiper-container > .pane {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0; }
      .detail .col-lg-6 > .container-fluid > .swiper-container img {
        margin: 0 auto; }
  .detail .col-lg-6 .gallery {
    padding-left: 0;
    margin-top: 20px;
    position: relative;
    overflow: hidden; }
    .detail .col-lg-6 .gallery > li {
      display: inline-block; }
      .detail .col-lg-6 .gallery > li a {
        font-size: 48px;
        vertical-align: middle; }
  .detail .col-lg-6 .attribute .btn {
    font-weight: 100; }
  .detail .col-lg-6 .color-group > .active {
    -moz-box-shadow: 0px 3px 0px rgba(0, 0, 0, 0.125) inset;
    -webkit-box-shadow: 0px 3px 0px rgba(0, 0, 0, 0.125) inset;
    -o-box-shadow: 0px 3px 0px rgba(0, 0, 0, 0.125) inset;
    box-shadow: 0px 3px 0px rgba(0, 0, 0, 0.125) inset; }

/* CART
-------------------------------------------------- */
.cart table td {
  vertical-align: middle; }
